@media (max-width: 1439px) {
    .slider-nav {
        top: auto;
        bottom: -90px; }

    .swiper-button-prev {
        left: calc( 50% - 70px ); }

    .swiper-button-next {
        right: calc( 50% - 70px ); }

    .slider {
        margin-bottom: 200px; } }

@media (max-width: 1199px) {
    .header-img-wrap {
        padding-bottom: 930px; }

    .fir {
        bottom: -90px; }

    .header-title  .new-line {
        display: inline; }

    .header-title-bordered {
        width: 170px;
        margin-left: 10px; }

    .header-txt-container {
        grid-template: auto/3fr 1fr; }

    .care-fertilizer-right {
        top: 10140px;
        right: 20px;
        width: 130px; }

    .care-content {
        max-width: 830px;
        left: 0; }

    .history-container {
        grid-template: auto/3fr 1fr; }

    .history-bg {
        bottom: -40px;
        left: 85%; }

    .history-img {
        top: 65%;
        left: 85%; }

    .delivery-right-cone {
        width: 100px; }

    .delivery-left-cone {
        width: 160px;
        left: 0; }

    .advantages-list {
        grid-template: auto/1fr 1fr 1fr; }

    .advantages-cone {
        width: 170px; }

    .history-cone {
        width: 160px; }

    .advantages {
        padding-bottom: 300px; }

    .catalog-left-cone,
    .catalog-right-cone,
    .bonus-catalog-left-cone,
    .care-fertilizer-left {
        display: none !important; } }

@media (max-width: 991px) {
    .logo-part {
        flex-direction: column;
        align-items: flex-start; }

    .logo-desc {
        margin: 22px 0 0; }

    .contacts-part {
        flex-direction: column-reverse;
        align-items: flex-end; }

    .menu-button {
        padding: 25px 20px;
        margin: 0 0 15px; }

    .menu-phone {
        text-align: right; }

    .container {
        max-width: 720px; }

    .header-title {
        font-size: 34px; }

    .why-fir {
        position: absolute;
        bottom: 0;
        right: 0;
        transform: translateX(50%);
        z-index: 15; }

    .tooltip {
        position: relative;
        padding-left: 30px !important;
        padding-right: 45px !important;
        top: auto !important;
        right: auto !important;
        left: auto !important;
        margin-bottom: 30px; }

    .tooltip .tp-before {
        right: 0 !important;
        left: auto !important;
        top: 50%;
        transform: translate(50%,-50%); }

    .why {
        margin-bottom: 80px; }

    .why-right-fir {
        display: none !important; }

    .why-title {
        top: -50px;
        margin-bottom: 0; }

    .section-title {
        font-size: 36px; }

    .why-title span {
        font-size: 40px; }

    .section-sub-title {
        font-size: 24px; }

    .catalog-list {
        margin-top: 90px;
        grid-template: auto/1fr 1fr; }

    .care-fertilizer-right,
    .care-fir {
        display: none; }

    .care-title {
        margin-bottom: 55px; }

    .advantages-list {
        grid-template: auto/1fr 1fr; }

    .advantage-title {
        margin-bottom: 80px; }

    .history-img,
    .history-bg {
        left: 120%; }

    .note {
        font-size: 20px; }

    .footer {
        height: 900px; }

    .footer-logo {
        grid-area: logo;
        margin: 0; }

    .privacy {
        grid-area: privacy;
        margin: 0; }

    .address {
        grid-area: address;
        text-align: right; }

    .web-dev {
        grid-area: dev; }

    .footer-menu {
        display: grid;
        grid-row-gap: 30px;
        grid-template: auto / 1fr 1fr;
        grid-template-areas: 'logo address' 'privacy dev';
        grid-column-gap: 30px; }

    .feedback {
        transform: translateY(-60%); }

    .slide-img {
        width: 100%; } }

@media (max-width: 767px) {
    .container {
        max-width: 450px; }

    .header {
        background-position: center; }

    .header-txt-container {
        display: block; }

    .header-title {
        font-size: 28px;
        margin-bottom: 30px; }

    .header-title-bordered {
        display: none; }

    .header-img-wrap {
        padding-bottom: 190px; }

    .fir {
        width: 625px;
        right: -315px;
        left: auto; }

    .header-post-title {
        font-size: 16px;
        padding-left: 30px;
        &:before {
            width: 20px; } }

    .phone-link {
        font-size: 16px; }

    .full-time {
        font-size: 12px; }

    .menu-button {
        padding: 19px 20px;
        min-width: 170px;
        font-size: 11px; }

    .logo {
        width: 140px; }

    .gift-img {
        max-height: 60px; }

    .gift-desc {
        font-size: 12px; }

    .gifts {
        grid-column-gap: 20px;
        width: 310px; }

    .header-button {
        width: 300px;
        height: 70px;
        background-size: contain;
        font-size: 13px;
        span {
            font-size: 12px; } }

    .section-title {
        font-size: 28px; }

    .why-title span {
        font-size: 36px; }

    .tooltip {
        font-size: 14px;
        width: 220px;
        min-height: 120px;
        padding: 20px 30px 20px 20px;
        margin-bottom: 10px; }

    .tp-before {
        width: 50px;
        height: 50px;
        &:before {
            width: 36px;
            height: 36px;
            font-size: 20px; } }

    .why-fir {
        width: 650px;
        bottom: -50px; }

    .catalog-list {
        grid-template: auto/1fr;
        grid-column-gap: 90px;
        padding: 0 45px; }

    .section-title .section-sub-title {
        font-size: 18px; }

    .buy-button {
        width: 100%; }

    .care-title .section-sub-title {
        max-width: 330px; }

    .catalog {
        overflow: hidden; }

    .care {
        padding-bottom: 100px; }

    .care-content {
        position: static;
        box-shadow: none;
        padding: 0;
        max-width: 100%;
        font-size: 18px;
        &:before {
            display: none; } }

    .care-item {
        padding-left: 60px; }

    .care-snow {
        display: none; }

    .advantages-list {
        grid-template: auto/1fr; }

    .advantages-cone {
        width: 110px; }

    .advantages {
        background-size: auto;
        padding-bottom: 480px; }

    .history-container {
        position: relative;
        top: -150px;
        grid-template: auto/ 1fr; }

    .history-img-wrap {
        display: none; }

    .history-snow {
        display: none; }

    .history {
        margin-bottom: -330px;
        position: relative;
        z-index: 15; }

    .delivery-right-cone {
        display: none !important; }

    .delivery-left-cone {
        left: -80px; }

    .delivery-txt {
        text-align: left; }

    .note {
        margin-top: 55px;
        padding: 30px 40px;
        font-weight: 300; }

    .slider-left-cone {
        display: none !important; }

    #modal-feedback {
        padding: 40px; }

    .modal {
        margin: 30px auto 30px; }

    .modal-contact-link {
        width: 110px;
        height: 38px; }

    .modal-title {
        font-size: 22px; }

    .modal-input,
    .modal-submit {
        height: 66px; }

    #modal-thank {
        padding: 80px 50px; }

    .feedback {
        transform: none;
        position: relative;
        top: 0;
        left: 0;
        width: auto;
        height: auto;
        padding: 60px 40px;
        margin-top: 100px; }

    .footer-container {
        position: static;
        transform: none;
        height: auto; }

    .footer {
        height: auto; }

    .footer-menu {
        position: relative;
        z-index: 20; }

    .map-wrap {
        height: 600px; }

    .address .new-line:nth-last-child(1) {
        width: auto; }

    .privacy {
        font-size: 12px; }

    .footer-logo {
        height: 36px; }

    .footer-menu {
        align-items: center; }

    .feedback {
        padding: 40px 25px; }

    .feedback-title {
        font-size: 24px;
        margin-bottom: 20px; }

    .feedback-desc {
        font-size: 14px;
        margin-bottom: 30px; }

    .feedback-input,
    .feedback-submit {
        height: 55px; }

    .footer-menu {
        grid-template: auto/1fr;
        grid-template-areas: 'logo' 'address' 'privacy' 'dev'; }

    .footer-logo {
        display: block;
        margin: 0 auto; }

    .address {
        text-align: center; }

    .web-dev {
        justify-self: center;
        text-align: center; }

    .privacy {
        text-align: center; } }

@media (max-width: 480px) {
    .container {
        max-width: 100%; }
    .menu-container {
        grid-template: 1fr/1fr; }

    .logo-part {
        align-items: center; }

    .logo-desc {
        margin-bottom: 15px; }

    .contacts-part {
        align-items: center; }

    .menu-phone {
        text-align: center; }

    .header-title {
        font-size: 23px; }

    .header {
        background: none; }

    .gifts {
        width: auto;
        grid-template: auto/1fr;
        justify-content: center; }

    .header-txt-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center; }

    .section-title {
        font-size: 24px; }

    .why-title {
        top: -25px;
        span {
            font-size: 24px; } }

    .note {
        padding: 25px;
        font-size: 16px; }

    .modal {
        margin: 0;
        border-radius: 0;
        max-width: 100%; }

    .modal-title {
        font-size: 20px; }

    .modal-text {
        font-size: 14px;
        margin: 20px 0; }

    .modal-contacts {
        flex-direction: column; }

    .modal-contact-link {
        width: 130px;
        margin-bottom: 15px; }

    .modal-input, .modal-submit {
        height: 55px;
        font-size: 14px; }

    .close-modal {
        top: 15px;
        right: 15px; } }

@media (max-width: 424px) {
    .catalog-list {
        padding: 0; } }

@media (max-width: 374px) {
    #modal-feedback {
        padding: 50px 30px 30px; } }
