    padding: 60px 60px 70px; }

#modal-thank {
    text-align: center;
    padding: 110px 70px 120px; }

.modal-title {
    font-size: 30px;
    line-height: 130%;
    text-align: center;
    margin-bottom: 24px;
    span {
        display: inline-block;
        width: 100%;
        font-weight: 300; }
    & + .modal-text {
        margin: 24px 0; } }

.modal-text {
    font-weight: 300;
    font-size: 18px;
    line-height: 130%;
    text-align: center;
    margin-bottom: 30px; }

.modal-contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px; }

.modal-contact-link {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #DEDEDE;
    border-radius: 4px;
    width: 130px;
    height: 44px;
    font-weight: 300;
    font-size: 14px;
    line-height: 140%;
    color: #A7A7A7;
    transition: color .2s, border-color .2s;
    &:hover {
        border-color: #282828; } }

.modal-icon {
    width: 20px;
    height: 20px;
    margin-right: 10px; }

.modal-input {
    width: 100%;
    margin-bottom: 20px;
    background: #F4F4F4;
    box-shadow: inset 0px 3px 2px rgba(0, 0, 0, 0.02);
    border-radius: 6px;
    height: 80px;
    border: 0;
    text-align: center;
    font-weight: 300;
    font-size: 16px;
    text-align: center;
    padding: 0 20px;
    &::placeholder {
        font-weight: 300;
        font-size: 16px;
        color: #A7A7A7;
        opacity: 1;
        transition: opacity .3s; }
    &:focus, &:active {
        outline: 0;
        &::placeholder {
            opacity: 0; } } }

.modal-submit {
    margin-bottom: 0;
    height: 80px;
    font-size: 16px; }

#modal-thank {
    padding: 125px 50px 100px;
    .modal-text {
        margin-bottom: 0; } }

.thank-img {
    margin-bottom: 45px;
    width: 210px;
    height: auto; }

.close-modal {
    position: absolute;
    cursor: pointer;
    right: 30px;
    top: 30px;
    height: 24px;
    width: 24px;
    &:hover {
        &:before, &:after {
            background: darken(#BABABA, 20%); } }
    &:before, &:after {
        content: '';
        position: absolute;
        background: #BABABA;
        height: 1px;
        width: 24px;
        transition: background .3s;
        top: 50%;
        left: 0; }
    &:before {
        transform: rotate(-45deg) translateY(-0.5px); }
    &:after {
        transform: rotate(45deg) translateY(0.5px); } }

.lock {
    overflow: hidden; }

